const popupBtn = document?.querySelector('[data-btn-popup]');
const popupPayment = document?.querySelector('[data-main-popup]');
const popupClose = document?.querySelector('[data-close-popup]');
const popupCloseBtn = document?.querySelector('[data-close-popup-btn]');
const popupContainer = document?.querySelector('.popup-payment__container');
const body = document.querySelector('body');

popupBtn?.addEventListener('click', () => {
  popupContainer.style.display = 'block';
  popupPayment.style.display = 'block';
})

popupClose?.addEventListener('click', () => {
  popupContainer.style.display = 'none';
  popupPayment.style.display = 'none';
})

popupCloseBtn?.addEventListener('click', () => {
  popupContainer.style.display = 'none';
  popupPayment.style.display = 'none';
})
